@import url(https://fonts.googleapis.com/css?family=Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Manjari&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body, html {
  margin: 0 !important;
  overflow-y: hidden; 
}
button{
  cursor: pointer;
  border: none;
  background: none;
  font-family: 'Raleway'
}
button:focus {
  outline:0;
}
span{
  font-family: 'Manjari',sans-serif;

}
a{
  color: black;
  text-decoration: none;
}


::-webkit-scrollbar {
  width: 0px;
  background: transparent; /* make scrollbar transparent */
}

h1{
  font-family: 'Raleway', sans-serif;
  font-weight: bold;
  letter-spacing: 1.5px;
  font-size: 2rem;
  color: white;
}

/*Home page css*/
.home-main-container{
  display: grid;
  grid-template-columns: 0.8fr 5fr;
  grid-template-rows: 100vh;
  min-height: 100vh;
  z-index: -1;
}

.home-left-container{
  padding-top: 1.2rem !important;
}

.login-form{
  margin: 0 auto;
  margin-top: 3%;
  background: white;
  display: grid;
  grid-template-rows: auto-fit 1fr 60px;
  box-shadow: 1px 1px 5px grey;
  border-radius: 10px;
  min-width: 450px;
  width: 25%;
  z-index: 26;
  padding-bottom: 3rem;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}
.login-form input, .login-form button{
  margin-bottom: 10%;
  background: none;
  border: none;
  text-align: center;
  background: lightgrey;
  padding: 1rem 0 1rem 0;
  border-bottom: 1px lightgrey solid;
  font-family: 'roboto', sans-serif;
  font-size: 1rem;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}
.login-form button:hover{
  background: #255eb3;
}
.login-form button{
  background: #0c203d;
  color: white;
  margin-bottom: 5rem;
}

.login-form-bottom{
  display: block;
  padding: 2rem;
}
.login-form-bottom2 button{
  height: 50px;
  width: 85%;
  display: block;
  margin: 0 auto;
}




.login-form input::-webkit-input-placeholder{
  font-family: 'Roboto', sans-serif;
  text-align: center;
}




.login-form input::-moz-placeholder{
  font-family: 'Roboto', sans-serif;
  text-align: center;
}




.login-form input:-ms-input-placeholder{
  font-family: 'Roboto', sans-serif;
  text-align: center;
}




.login-form input::-ms-input-placeholder{
  font-family: 'Roboto', sans-serif;
  text-align: center;
}




.login-form input::placeholder{
  font-family: 'Roboto', sans-serif;
  text-align: center;
}

.container-header.login{
  padding-top: 1.5rem;
  padding: 0;
  padding-top: 1.5%;
  text-align: center;
  max-height: 98px;
  border-radius: 5px 10px 0 0;
  box-shadow: none;
  -webkit-border-radius: 5px 10px 0 0;
  -moz-border-radius: 5px 10px 0 0;
  -ms-border-radius: 5px 10px 0 0;
  -o-border-radius: 5px 10px 0 0;
}


.home-container{
  text-align: center;
  background: white;
  margin-top: 5%;
  background-size: 100% 100%;
  border-radius: 10px;
  box-shadow: none;
  min-height: 100vh;

}

#home-pic{
  width: 80%;
  height: 25rem;
}
/*End Home Page css */

.container{
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 100px auto;
  position: relative;
}

.wrapper{
  box-sizing: border-box;
  width: 100%;
  padding-bottom: 5rem;
  margin-left: auto;
  margin-right: auto;
}
.inside-wrapper{
  width: 100%;
  box-sizing: border-box;
}
.header{
  height: 60px;
  border-top: 5px #13305c solid;
  background: white;
  z-index: 25;
}

input:focus{
  background: white;
}

.header-text{
  padding: 0.9rem;
  padding-left: 0.9%;
  font-size: 1.6rem;
  width: 11.65%;
  border-right: 5px #13305c solid;
  border-left: 5px #13305c solid;
  color: #13305c;
  background: url(../../static/media/right.41bcaf32.png);
  z-index: 255;

}

.header-text span{
  font-family: 'Fredoka One', cursive !important;
}


.profile-container{
  z-index: 1;
  display: grid;
  grid-template-columns: 0.8fr 5fr;
  grid-template-rows: 100vh;
  min-height: 100vh;
}

.profile-left-container, .home-left-container{
  text-align: center;
  background: #13305c;
  z-index: 255;
}

.profile-left-container-header,
.profile-left-container-header h1{
  color: white !important;
  font-size: 1.2rem !important;
  text-align: start;
  padding-left: 2.5%;
}

.profile-left-container-header{
  padding: 1rem;
  background: #10294f;
  
}

/*Navigation css*/
.user-navbar, .home-buttons-container{
  display: grid;
  grid-template-columns: 1fr;
  margin: 0 auto;
  
  width: 100%;
}

.left-link{
  padding: 1rem;
  padding-left: 8%;
  background: red;
  color: white !important;
  transition: 0.1s ease-in;
  -webkit-transition: 0.1s ease-in;
  -moz-transition: 0.1s ease-in;
  -ms-transition: 0.1s ease-in;
  -o-transition: 0.1s ease-in;
}

.home-buttons-container button:hover,
.left-link:hover{
  font-size: 1.45rem;
  text-shadow: 1px 1px 5px black;
}
.active{
  color:  white !important;
  background: #255eb3 !important;
}
#user-button h1{
  font-family: 'roboto', sans-serif !important;
}
