#addcard-button:before, #editcard-button:before, #user-button:before,
#logout-button:before, #studycard-button:before, #quizcard-button:before{
  content: '';
  display: inline-block;
  position: relative;
  background: url(data:image/png;base64,);
  background-size: 25px 25px;
  height: 25px;
  width: 25px;
  margin-right: 5%;
  top: 0.25rem;
}
#login-button:before{
  background: url(data:image/png;base64,);
  background-size: 25px 25px;
}
#register-button:before{
  background: url(../../static/media/register.3785f76c.png);
  background-size: 25px 25px;
}
#addcard-button:before{
  background: url(data:image/png;base64,);
  background-size: 25px 25px;
}
#editcard-button:before{
  background: url(data:image/png;base64,);
  background-size: 25px 25px;
}
#user-button:before{
  background: url(data:image/png;base64,);
  background-size: 25px 25px;
  top: 0.4rem;
}
#logout-button:before{
  background: url(data:image/png;base64,);
  background-size: 25px 25px;
}
#studycard-button:before{
  background: url(data:image/png;base64,);
  background-size: 25px 25px;
}
#quizcard-button:before{
  background: url(data:image/png;base64,);
  background-size: 25px 25px;
}
.user-navbar button, .user-navbar a,
.home-buttons-container button{
  cursor: pointer;
  display: inline-block;
  background: none;
  border: none;
  color: grey;
  font-family: 'Roboto', sans-serif;
  text-align: start;
  text-decoration: none;
  margin-bottom: 2%;
  font-size: 1.3rem;

}
/*End Navigation CSS */

.profile-right-container,
.home-right-container{
  overflow-y: scroll;
  height: auto;
  margin-top: -3.75rem;
  background: url(../../static/media/right.41bcaf32.png);
  z-index: 5;
}

.container-header{
  padding-left: 3.8rem;
  margin-bottom: 5%;
  line-height: 19px;
  min-height: 62px;
  margin-bottom: 10%;
  background: white;
  color: black;
  box-shadow: 1px 1px 15px lightgrey;
}
.container-header h1{
  color: #0c203d;
}
.container-header.profile{
  display: grid;
  grid-template-columns: 1fr 1.2fr;
  background: white;
  box-shadow: 1px 1px 15px lightgrey;
}
.container-header.profile h1{
  color: #0c203d;
}
/*Container Middle Header  */
.container-middle-header{
  display: grid;
}
/*End Container Middle Header */

/*Search Box Styles */
.search-input{
  border: none;
  color: rgb(124, 123, 123);
  width: 100%;
  height: 0.5rem;
  background: #f1f3f4;
  padding: 1.1rem 0 1rem 0;
  outline: none;
  display: block;
  font-weight: bold;
  border-radius: 4px;
  text-align: center;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
}
.search-input:focus{
  background: white;
  color: black;
  box-shadow: 0px 0px 3px darkgrey;
}
.search-input::-webkit-input-placeholder{
  color: grey;
  font-family: 'Roboto', sans-serif;
  text-align: center;
}
.search-input::-moz-placeholder{
  color: grey;
  font-family: 'Roboto', sans-serif;
  text-align: center;
}
.search-input:-ms-input-placeholder{
  color: grey;
  font-family: 'Roboto', sans-serif;
  text-align: center;
}
.search-input::-ms-input-placeholder{
  color: grey;
  font-family: 'Roboto', sans-serif;
  text-align: center;
}
.search-input::placeholder{
  color: grey;
  font-family: 'Roboto', sans-serif;
  text-align: center;
}
.searchbox-container{
  margin-top: 0.9%;
  justify-self: center;
  border-radius: 8px;
  min-width: 400px;
}

.searchbox-container::before{
  background: url(data:image/png;base64,);
  background-size: 25px 25px;
  content: '';
  margin-top: 0.55%;
  margin-left: 0.9%;
  position: absolute;
  display: inline;
  height: 25px; width: 25px;
}
/*End Search Box Styles */
/*Flash card styles*/
.flashcard-container{
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 95%;
  margin: 0 auto;
  margin-bottom: 15rem;
  grid-auto-rows: 320px;
  grid-row-gap: 2%;
  grid-column-gap: 1.5%;
  padding: 1rem;
}

.flashcard{
  box-shadow: 0px 1px 8px grey;
  border: #cc9124 solid;
  border-width: 0px 0px 5px 0px;
  width: 90%;
  justify-self: center;
  height: 100%;
  z-index: 155;
  display: grid;
  background: white;
  border-radius: 11px;
  left: 0;
  grid-template-columns: 1fr;
  grid-auto-rows: minmax(auto,200px);
  -webkit-animation: fadeIn 0.3s ease-in;
          animation: fadeIn 0.3s ease-in;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  -webkit-border-radius: 11px;
  -moz-border-radius: 11px;
  -ms-border-radius: 11px;
  -o-border-radius: 11px;
}
.star{
  box-sizing: border-box;
  padding: 0rem 1rem 0.2rem 1rem;
  color: darkgrey;
  font-size: 3rem;
  max-height: 10px;
  color: darkgrey;
}

.star.yellow{
  color: #ffb428;
}

.flashcard-header{
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.flashcard-header-right{
  justify-self: end;
  padding: 0.2rem 0.3rem 0.5rem 1rem;
}

.flashcard-header-right img{
  opacity: 0.5;
}

.flashcard-header-right-content{
  display: inline-block;
  position: absolute;
  padding: 1rem;
  top: 5%;
  right: 0;
  border-radius: 5px;
  z-index: -1;
  opacity: 0;
}

.flashcard-header-right-content.show{
  opacity: 1;
}

#xxx{
  cursor: pointer;
}

@-webkit-keyframes fadeIn{
  from{
    -webkit-transform: translateY(500px);
            transform: translateY(500px);
  }
  to{
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes fadeIn{
  from{
    -webkit-transform: translateY(500px);
            transform: translateY(500px);
  }
  to{
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

.flashcard-buttons{
  margin: 2%;
  cursor: pointer;
  align-self: end;
  justify-self: end;
}
.flashcard-title,
.flashcard-subject{
  text-align: center;
  max-height: 85px;
  height: 85px;
  font-size: 1.6rem;
}

.flashcard button,
.question-buttons-container button{
  background: none;
  border: none;
  cursor: pointer;
  color: grey;
  -webkit-transition: 1s ease;
  transition: 1s ease;
  font-family: 'roboto', sans-serif;
}

.flashcard button:hover,
.question-buttons-container button:hover{
  color: #2764bf;
}

.editable{
  font-family: 'Manjari', sans-serif;
  font-weight: lighter;
  padding: 0.1rem;
  padding-top: 0.5rem;
  font-size: 1.3rem;
}

.editable.show{
  border: 3px lightblue dotted;
  border-width: 3px 0 3px 0;
}

.flashcard label{
  border-bottom: 0.2px black solid;
  font-family: 'Manjari', sans-serif;
  padding: 0rem;
}

/*Question styles*/
.question-main-container{
  display: grid;
  grid-template-columns: 2.5fr 1fr;
  width: 94%;
  margin: 0 auto;
}
.addquestion-container{
  box-sizing: border-box;
  top: 12.3%;
  right: 2.3%;
  padding: 0 1rem 0 1rem;
  position: fixed;
  background: #1e498a;
  width: 21%;
  height: 500px;
  margin: 0 auto;
  margin-bottom: 3%;
  border-radius: 7px;
  box-shadow: 1px 2px 5px grey;
}

.addquestion-container h1{
  color: white;
  text-align: center;
}

.question-container{
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr;
  width: 100%;
  margin: 0 auto;
  grid-auto-rows: 320px;
  grid-row-gap: 2%;
  grid-column-gap: 2%;
  padding: 1rem;
  margin-bottom: 20rem;
}
.question{
  box-sizing: border-box;
  display: grid;
  width: 100%;
  justify-self: center;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 300px 50px;
  grid-row-gap: 1rem;
  grid-column-gap: 1%;
 
}

.question-question, .question-answer{
  box-shadow: 0px 1px 5px grey;
  background: white;
  height: 100%;
  max-width: 550px;
  display: grid;
  grid-auto-columns: 1fr;
  justify-items: center;
}

.editable{
  position: relative;
  text-align: center;
}

pre{
  white-space: normal !important;
}

.question-buttons-container{
  grid-column: 2/3;
  justify-self: end;
  height: 10%;
}

.question label{
  color: #c4c2c1;
  height: 2rem;
  border-bottom: 2px #0c203d solid;
  width: 100%;
  text-align: center;
  padding-top: 1rem;
  font-family: 'Roboto', sans-serif;

}

.addquestion-form{
  display: grid;
  grid-template-columns: 1fr;
  grid-column-gap: 5%;
  grid-row-gap: 2.5%;
  justify-items: center;
}
#question, #answer{
  width: 100%;
  box-shadow: 0px 0px 2px grey;

  height: 150px;
}
.addquestion-form-container,
.addanswer-form-container{
  width: 100%;
  justify-self: center;
}
#question, #answer{
  padding: 1rem;
  box-sizing: border-box;
  font-size: 1rem;
  font-family: 'Manjari', sans-serif;
}

#question::-webkit-input-placeholder,
#answer::-webkit-input-placeholder{
  text-align: center;
  padding-top: 3.4rem;
  font-size: 1rem;
  font-family: 'Manjari', sans-serif;
}

#question::-moz-placeholder,
#answer::-moz-placeholder{
  text-align: center;
  padding-top: 3.4rem;
  font-size: 1rem;
  font-family: 'Manjari', sans-serif;
}

#question:-ms-input-placeholder,
#answer:-ms-input-placeholder{
  text-align: center;
  padding-top: 3.4rem;
  font-size: 1rem;
  font-family: 'Manjari', sans-serif;
}

#question::-ms-input-placeholder,
#answer::-ms-input-placeholder{
  text-align: center;
  padding-top: 3.4rem;
  font-size: 1rem;
  font-family: 'Manjari', sans-serif;
}

#question::placeholder,
#answer::placeholder{
  text-align: center;
  padding-top: 3.4rem;
  font-size: 1rem;
  font-family: 'Manjari', sans-serif;
}

#add-button{
  background: none;
  background: white;
  border: none;
  width: 100%;
  height: 50px;
  font-family: 'Roboto',sans-serif;
  font-weight: bold;
}

/*End Question styles*/

/*Study Styles*/
.flashcard.study{
  justify-self: center;
  box-shadow: 0px -10px 15px lightgrey;
  grid-template-rows: 35px 1fr 1.2fr ;
  height: 360px;
  -webkit-transition: 0.2s ease-in-out;
  transition: 0.2s ease-in-out;
}

.orderby-buttons-container.study{
  margin-top: -2%;
}

.flashcard.study:hover{
  -webkit-transform:scale(1.1);
          transform:scale(1.1);
}


.xx{
  display: grid;
}
.view-quiz-header{
  border-radius: 8px;
  width: 88%;
  margin: 0 auto;
  min-height: 10rem;
  line-height: 25px;
  margin-bottom: -3%;
  font-family: 'Manjari',sans-serif;
}

.view-quiz-header h1{
  color: #0c203d;
  font-family: 'Manjari',sans-serif;
}
.view-quiz-header p{
  font-size: 1.2rem;
}
.question-container.quiz{
  margin-top: 3%;
  position: relative;
  justify-self: center;
  max-height: 200px;
  width: 85%;
  text-align: center;
  -webkit-perspective: 1000px;
          perspective: 1000px;
}

.question.study{
  grid-template-columns: 1fr;
  width: 50%;
  justify-self: center;
  margin: 0 auto;
  -webkit-transition: 0.5s ease;
  transition: 0.5s ease;

}


.question-question.question{
  align-items: center;
  min-height: 350px;
  -webkit-transition: 0.5s ease;
  transition: 0.5s ease;
  border-radius: 8px;
}

.question-container-question{
  margin: 0 auto;
  justify-self: center;
  width: 100%;
}

.quiz-buttons-container{
  position: relative;
  width: 100%;
  margin: 0 auto;
  bottom: 180%;
  text-align: center;
}
.quiz-buttons-container button{
  background: none;
  border: none;
  color: #0c203d;
  margin-left: 5%;
  margin-right: 5%;
  font-size: 2rem;
}
.quiz-buttons-container> span{
  display: inline-block;
  bottom: 0.25rem;
  position: relative;
  font-family: 'Roboto', sans-serif;
  font-weight: bolder;

}
.question-question.studycard{
  -webkit-perspective: 1000px;
          perspective: 1000px;
  min-height: 350px;
  max-width: 820px;
  position: relative;
  -webkit-animation: flipQuestionY 0.3s linear;
          animation: flipQuestionY 0.3s linear;
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}
.question-question.studycard .editable{
  font-size: 1.8rem;
}

.question-question.studycardx{
  -webkit-animation: flipQuestionx 0.3s linear;
          animation: flipQuestionx 0.3s linear;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  min-height: 350px;
  max-width: 820px;
  justify-self: stretch;
  position: relative;
  -webkit-transform: rotateX(0deg);
          transform: rotateX(0deg);

}



@-webkit-keyframes flipQuestionY{
  from{
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg);
  }
  translate{
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
  }
}



@keyframes flipQuestionY{
  from{
    -webkit-transform: rotateY(-180deg);
            transform: rotateY(-180deg);
  }
  translate{
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
  }
}

@-webkit-keyframes flipQuestionx{
  from{
    -webkit-transform: rotateX(-180deg);
            transform: rotateX(-180deg);
  }
  translate{
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }
}

@keyframes flipQuestionx{
  from{
    -webkit-transform: rotateX(-180deg);
            transform: rotateX(-180deg);
  }
  translate{
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }
}

.question-question.studycard button,
.question-question.studycardx button{
  background: #f2f0ee;
  color: #0c203d;
  font-weight: bolder;
  padding: 0.5rem;
  height: 40%;
  top: 50%;
  width: 30%;
  border: none;
  position: relative;
  font-family: 'Roboto', sans-serif;
}

#question-count{
  font-family: 'Manjari', sans-serif;
  position: relative;
  bottom: 3%;
}



/*Study Styles*/
.quiz-score-container{
  font-family: 'Raleway',sans-serif;
  margin: 0 auto;
  width: 90%;
  margin-top: -1%;
  text-align: center;
  font-size: 1.8rem;
  min-height: 50px;
}

.quiz-score-container span{
  display: inline-block;
  margin-left: 5%;
  margin-right: 5%;
}
.quizgame-buttons-container{
  width: 80%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 5%;
  grid-column-gap: 5%;
  grid-auto-rows: minmax(200px, 200px);
}

.quizgame-buttons-container button{
  border: none;
