  border: 2px #0c203d solid;
  border-radius: 5px;
  margin: 0 auto;
  color: black;
  width: 40rem; height: 20rem;
  font-size: 2rem;
  -webkit-animation: fadeIn 0.3s ease-in;
          animation: fadeIn 0.3s ease-in;
  box-shadow: 2px 2px 7px darkgrey;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 4fr 1fr;
}

.modal-header{
